<template>
  <div class="mt-5">
    <h2 class="text-d ml-4 font-semibold">Filter by</h2>

    <div class="content mt-5 rounded-md overflow-hidden">
      <h2 class="font-medium p-4">Your budget per day</h2>

      <div class="flex justify-between items-center mt-4">
        <div class="flex items-center gap-2">
          <input
            type="checkbox"
            v-model="modelfor0"
            @change="getDataForRange1"
            class="ml-4"
            id="0-200"
          />
          <label for="0-200" class="text-sm">$ 0 - $ 200</label>
        </div>
      </div>

      <div class="flex justify-between items-center mt-4">
        <div class="flex items-center gap-2">
          <input
            type="checkbox"
            v-model="modelfor200"
            @change="getDataForRange2"
            class="ml-4"
            id="200-500"
          />
          <label for="200-500" class="text-sm">$ 200 - $ 500</label>
        </div>
      </div>

      <div class="flex justify-between items-center mt-4">
        <div class="flex items-center gap-2">
          <input
            type="checkbox"
            v-model="modelfor500"
            @change="getDataForRange3"
            class="ml-4"
            id="500-1000"
          />
          <label for="500-1000" class="text-sm"> $ 500 - $ 1,000</label>
        </div>
      </div>

      <div class="flex justify-between items-center mt-4">
        <div class="flex items-center gap-2">
          <input
            type="checkbox"
            v-model="modelfor1000"
            @change="getDataForRange4"
            class="ml-4"
            id="1000-2000"
          />
          <label for="1000-2000" class="text-sm">$ 1,000 - $ 2,000 </label>
        </div>
      </div>

      <div class="flex justify-between items-center mt-4">
        <div class="flex items-center gap-2">
          <input
            type="checkbox"
            v-model="modelfor2000"
            @change="getDataForRange5"
            class="ml-4"
            id="2000-5000"
          />
          <label for="2000-5000" class="text-sm"> $ 2,000 - $ 5,000 </label>
        </div>
      </div>

      <div class="enterBudget">
        <div
          class="setBudget flex justify-between items-center mt-5 mb-4 ml-4 text-sm"
        >
          <p>Set your own budget</p>
          <div class="container mr-4">
            <input
              type="checkbox"
              class="checkbox"
              id="checkbox"
              @click="showMinMaxInputs"
            />
            <label class="switch" for="checkbox">
              <span class="slider"></span>
            </label>
          </div>
        </div>
      </div>
      <transition name="MaxMin">
        <div v-show="controlMinMax">
          <form class="flex justify-center items-center gap-9" @submit.prevent>
            <input
              type="number"
              class="w-1/2 p-3 ml-5 inputs outline-none text-xs"
              placeholder="Min budget"
              v-model="minBudget"
              @keydown="userBudget($event)"
            />
            <input
              type="number"
              class="w-1/2 p-3 mr-5 inputs outline-none text-xs"
              placeholder="Max budget"
              v-model="maxBudget"
              @keydown="userBudget($event)"
            />
          </form>
          <p class="text-[12px] text-[#4F4F4F] mt-2 ml-[25px] mb-5">
            Press Enter To Search
          </p>
        </div>
      </transition>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";
import ApiCall from "../APIFunction/SearchByBudget.js";
import { useAPIData } from "@/stores/APIDataStore";
const dataStore = useAPIData();

const modelfor0 = ref(false);
const modelfor200 = ref(false);
const modelfor500 = ref(false);
const modelfor1000 = ref(false);
const modelfor2000 = ref(false);
const controlMinMax = ref(false);
const minBudget = ref(null);
const maxBudget = ref(null);

const showMinMaxInputs = function () {
  controlMinMax.value = !controlMinMax.value;
  modelfor200.value = false;
  modelfor500.value = false;
  modelfor1000.value = false;
  modelfor2000.value = false;
  modelfor0.value = false;
};

const userBudget = function (e) {
  if (e.key === "Enter") {
    if (maxBudget.value >= minBudget.value)
      ApiCall(minBudget.value, maxBudget.value);
    else if (minBudget.value > maxBudget.value) {
      minBudget.value += maxBudget.value;
      maxBudget.value = minBudget.value - maxBudget.value;
      minBudget.value = minBudget.value - maxBudget.value;
      ApiCall(minBudget.value, maxBudget.value);
    } else {
      dataStore.currentPage = 1;
      dataStore.hotelSearchData = dataStore.realDataForBudget;
      dataStore.metaData = dataStore.fixMetaData;
    }
  }
};

const getDataForRange1 = function () {
  if (modelfor0.value) {
    modelfor200.value = false;
    modelfor500.value = false;
    modelfor1000.value = false;
    modelfor2000.value = false;
    controlMinMax.value = false;
    dataStore.fixMetaData = dataStore.metaData;
    ApiCall(0, 200);
  } else {
    dataStore.currentPage = 1;
    dataStore.hotelSearchData = dataStore.realDataForBudget;
    dataStore.metaData = dataStore.fixMetaData;
  }
};

const getDataForRange2 = function () {
  if (modelfor200.value) {
    modelfor0.value = false;
    modelfor500.value = false;
    modelfor1000.value = false;
    modelfor2000.value = false;
    controlMinMax.value = false;
    dataStore.fixMetaData = dataStore.metaData;
    ApiCall(200, 500);
  } else {
    dataStore.currentPage = 1;
    dataStore.hotelSearchData = dataStore.realDataForBudget;
    dataStore.metaData = dataStore.fixMetaData;
  }
};

const getDataForRange3 = function () {
  if (modelfor500.value) {
    modelfor200.value = false;
    modelfor0.value = false;
    modelfor1000.value = false;
    modelfor2000.value = false;
    controlMinMax.value = false;

    dataStore.fixMetaData = dataStore.metaData;
    ApiCall(500, 1000);
  } else {
    dataStore.currentPage = 1;
    dataStore.hotelSearchData = dataStore.realDataForBudget;
    dataStore.metaData = dataStore.fixMetaData;
  }
};

const getDataForRange4 = function () {
  if (modelfor1000.value) {
    modelfor200.value = false;
    modelfor500.value = false;
    modelfor0.value = false;
    modelfor2000.value = false;
    controlMinMax.value = false;

    dataStore.fixMetaData = dataStore.metaData;
    ApiCall(1000, 2000);
  } else {
    dataStore.currentPage = 1;
    dataStore.hotelSearchData = dataStore.realDataForBudget;
    dataStore.metaData = dataStore.fixMetaData;
  }
};

const getDataForRange5 = function () {
  if (modelfor2000.value) {
    modelfor200.value = false;
    modelfor500.value = false;
    modelfor1000.value = false;
    modelfor0.value = false;
    dataStore.fixMetaData = dataStore.metaData;
    controlMinMax.value = false;

    ApiCall(2000, 5000);
  } else {
    dataStore.currentPage = 1;
    dataStore.hotelSearchData = dataStore.realDataForBudget;
    dataStore.metaData = dataStore.fixMetaData;
  }
};
</script>

<style scoped>
.MaxMin-enter-from {
  opacity: 0;
  transform: translateY(-30px);
}
.MaxMin-leave-to {
  opacity: 0;
  transform: translateY(30px);
}
.MaxMin-enter-active {
  transition: all 0.3s ease-out;
}
.MaxMin-leave-active {
  transition: all 0.3s ease-in;
}
.MaxMin-enter-to,
.MaxMin-leave-from {
  opacity: 1;
  transform: translateY(0);
}

.content,
.font-medium {
  border: 1px solid #e0e0e0;
}
.font-medium {
  background-color: rgba(242, 242, 242, 1);
}

.component-title {
  width: 100%;
  position: absolute;
  z-index: 999;
  top: 30px;
  left: 0;
  padding: 0;
  margin: 0;
  font-size: 1rem;
  font-weight: 700;
  color: #888;
  text-align: center;
}

/* The switch - the box around the slider */
.container {
  width: 32px;
  height: 20px;
  position: relative;
}

/* Hide default HTML checkbox */
.checkbox {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
}

.switch {
  width: 100%;
  height: 100%;
  display: block;
  background-color: #e9e9eb;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.2s ease-out;
}

/* The slider */
.slider {
  width: 15px;
  height: 15px;
  position: absolute;
  left: calc(50% - 14px / 2 - 10px);
  top: calc(50% - 15px / 2);
  border-radius: 50%;
  background: #ffffff;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.15), 0px 3px 1px rgba(0, 0, 0, 0.06);
  transition: all 0.2s ease-out;
  cursor: pointer;
}

.checkbox:checked + .switch {
  background-color: var(--mainBlue);
}

.checkbox:checked + .switch .slider {
  left: calc(50% - 15px / 2 + 10px);
  top: calc(50% - 15px / 2);
}

.inputs {
  border: 1px solid #e0e0e0;
  background: #fff;
}
</style>
